<template>
  <div class="detail">
    <div class="topBgcolor"></div>
    
    <!-- 导航 -->
    <van-nav-bar
      title="商品详情"
      left-text="返回"
      left-arrow
      :fixed="true"
      @click-left="back"
    />

    <!-- 大图 -->
    <div class="detail-box">
      <img
        class="auto-img"
        :src="product.large_img"
        alt=""
      >
      <div class="pro-name">
        <span>{{product.name}}</span>
      </div>
    </div>

    <div class="detail-info">
      <!-- 商品属性 -->
      <div class="rule-box">
        <div
          class="rule-item clearfix"
          v-for="(item, index) in product.rules"
          :key="index"
        >
          <div class="fl rule-item-title">{{item.title}}：</div>
          <div class=" fl clearfix">
            <div
              class="fl rule-item-tag"
              :class="{active:v.isActive}"
              v-for="(v, i) in item.rules"
              :key="i"
              @click="toggleRule(v,index)"
            >{{v.title}}</div>
            <!-- <div class="fl rule-item-tag">半糖</div> -->
          </div>
        </div>
      </div>
      <!-- 商品描述 -->
      <div class="dec-box">
        <div class="desc-title">商品描述</div>
        <div class="desc-content">
          <div
            class="desc-text"
            v-for="(item,index) in product.desc"
            :key="index"
          >{{index+1}}、{{item}}</div>
        </div>
      </div>
    </div>

    <!-- 价格 -->
    <div class="clearfix desc-price-count">
      <div class="fl price">￥{{product.price}}</div>
      <div class="fr count">
        <van-stepper
          v-model="count"
          theme="round"
          button-size="24"
          disable-input
        />
      </div>
    </div>

    <!-- 底部 -->
    <van-goods-action>
      <van-goods-action-icon
        icon="shop"
        text="店铺"
        color="#7c90cf"
        @click="backHome"
      />
      <van-goods-action-icon
        icon="shopping-cart"
        text="购物车"
        :badge="cartNum == 0 ? '' : cartNum"
        color="#7c90cf"
        @click="goShopcart"
      />
      <van-goods-action-icon
        icon="star"
        text="收藏"
        :color="isLike ? '#0b34ba' : '#7c90cf'"
        @click="likeAndNotLike"
      />
      <van-goods-action-button
        type="warning"
        text="加入购物车"
        color="#7c90cf"
        @click="addShopbag"
      />
      <van-goods-action-button
        type="danger"
        text="立即购买"
        color="#0b34ba"
        @click="buyNow"
      />
    </van-goods-action>

  </div>
</template>

<script>
import "../assets/less/detail.css";
import { utils } from "../assets/js/utils";

export default {
  data() {
    return {
      pid: "",
      count: 1,
      product: {},
      isLike: false,
      cartNum: 0,
    };
  },
  created() {
    this.pid = this.$route.query.pid;
    this.getProductByPid();

    this.findLike();
    this.findShopbag();
  },

  methods: {
    // 跳转上一步
    back() {
      this.$router.go(-1);
    },

    // 跳转首页
    backHome() {
      this.$router.push({ name: "Home" });
    },

    // 跳到购物车
    goShopcart() {
      this.$router.push({ name: "Shopbag" });
    },

    // 根据商品pid获取商品数据
    getProductByPid() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/productDetail",
        // 如果是get请求，参数需放在params,如果是post请求，参数需放在date
        params: {
          appkey: this.appkey,
          pid: this.pid,
        },
      })
        .then((result) => {
          this.$toast.clear();
          //   
          if (result.data.code == 600) {
            //   处理商品描述数据
            result.data.result[0].desc = result.data.result[0].desc.split(/\n/);

            // 处理商品规格数据
            let ruleItems = ["tem", "sugar", "cream", "milk"];
            let rulesData = [];
            ruleItems.map((v) => {
              if (result.data.result[0][v] == "") {
                return;
              }
              let rule = {
                title: result.data.result[0][v + "_desc"],
                rules: [],
              };

              let r = result.data.result[0][v].split("/");
              r.map((value, index) => {
                let o = {
                  title: value,
                  isActive: index == 0,
                };
                rule.rules.push(o);
              });
              rulesData.push(rule);
            });
            result.data.result[0].rules = rulesData;
            // ===============================

            this.product = result.data.result[0];

            // 
            
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    // 切换商品规格
    toggleRule(v, index) {
      if (v.isActive) {
        return;
      }

      let ruleData = this.product.rules[index].rules;
      for (let i = 0; i < ruleData.length; i++) {
        if (ruleData[i].isActive) {
          ruleData[i].isActive = false;
          break;
        }
      }

      v.isActive = true;
    },

    // 查询商品是否被收藏
    findLike() {
      // 获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findlike",
        // 如果是get请求，参数需放在params,如果是post请求，参数需放在date
        params: {
          appkey: this.appkey,
          tokenString,
          pid: this.pid,
        },
      })
        .then((result) => {
          this.$toast.clear();
          

          if (result.data.code == 1000) {
            
            if (result.data.result.length == 1) {
              
              this.isLike = true;
              
            }
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    // 收藏或取消收藏
    likeAndNotLike() {
      // 获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        this.$toast('请先登录！');
        return this.$router.push({ name: "Login" });
      }

      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        pid: this.pid,
      });

      let url = this.isLike ? "/notlike" : "/like";

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url,
        // 如果是get请求，参数需放在params,如果是post请求，参数需放在date
        data,
      })
        .then((result) => {
          this.$toast.clear();
          

          if (result.data.code == 700) {
            return this.$router.push({ name: "Login" });
          }
          this.$toast(result.data.msg);

          if (result.data.code == 800) {
            this.isLike = true;
            // 
          } else if (result.data.code == 900) {
            this.isLike = false;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    // 查询购物车数目
    findShopbag() {
      // 获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findAllShopcart",
        // 如果是get请求，参数需放在params,如果是post请求，参数需放在date
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();
          //   

          if (result.data.code == 5000) {
            this.cartNum = result.data.result.length;
          } else {
            this.$toast(result.data.msg);
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    // 加入购物车
    addShopbag(isBuy) {
      // 获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        this.$toast('请先登录！');
        return this.$router.push({ name: "Login" });
      }

      //   获取选择的规格
      let rules = this.product.rules;
      //   

      let rule = [];

      rules.map((v) => {
        for (let i = 0; i < v.rules.length; i++) {
          if (v.rules[i].isActive) {
            rule.push(v.rules[i].title);
            break;
          }
        }
      });

      // 序列化参数
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        pid: this.pid,
        count: this.count,
        rule: rule.join("/"),
      });

      //   

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url: "/addShopcart",
        // 如果是get请求，参数需放在params,如果是post请求，参数需放在date
        data,
      })
        .then((result) => {
          this.$toast.clear();
          // 

          if (result.data.code == 3000) {
            if (result.data.status == 1) {
              // 累加徽章数量,购物车条数，而不是商品总数量
              this.cartNum++;
            }
            if (isBuy === true) {
              //立即购买
              setTimeout(() => {
                this.$router.push({
                  name: "Pay",
                  query: { sids: result.data.sid },
                });
              }, 600);
            }
          }
          if (isBuy != true) {
            this.$toast(result.data.msg);
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    //立即购买
    buyNow() {
      this.addShopbag(true);
    },
  },
};
</script>
